/****************************** style ******************************/
body,
button,
input,
select,
textarea {
  font: 14px/1.4 "\5FAE\8F6F\96C5\9ED1", "arial";
  color: #5a5a5a;
  font-size: 14px;
}

html {
  min-width: 1200px;
}

.center {
  width: 1200px;
  margin: 0 auto;
}

.icon {
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  zoom: 1;
  *display: inline;
}

.header {
  height: 160px;
  background: url(../img/head_bg.jpg) center repeat-y;
  position: relative;
  z-index: 99;
}

.header .head_main {
  height: 100%;
}

.header .head_main .center {
  height: 100%;
}

.header .head_main .fl {
  height: 100%;
  padding-top: 40px;
  padding-right: 20px;
  background: #c21a29;
  color: #fff;
}

.header .head_main .fl img, .header .head_main .fl .head_tit, .header .head_main .fl .head_text {
  float: left;
}

.header .head_main .fl img {
  margin-top: 15px;
  margin-right: 15px;
}

.header .head_top .fr {
  line-height: 28px;
}

.header .head_top .icon {
  margin: 3px;
  vertical-align: top;
}

.header .head_tit {
  line-height: 1.2;
  margin-top: 5px;
}

.header .head_tit span {
  font-size: 37px;
}

.header .head_tit strong {
  display: block;
  font-size: 32px;
}

.header .head_text {
  text-align: center;
  margin-left: 15px;
  margin-top: 25px;
  line-height: 1.2;
}

.header .head_text span {
  font-size: 20px;
  font-family: NSimSun;
}

.header .nav {
  margin-top: 15px;
}

.header .nav:hover > li .active {
  color: #434343;
  background: none;
}

.header .nav > li {
  float: left;
  width: 108px;
  height: 70px;
  padding: 0 5px;
  line-height: 70px;
  text-align: center;
  color: #434343;
}

.header .nav > li:hover ul {
  display: block;
}

.header .nav > li > a {
  display: block;
}

.header .nav > li > a:hover, .header .nav > li > a.active, .header .nav > li > a.active:hover {
  color: #c21a29;
  background: url(../img/nav_hover.jpg) no-repeat center bottom;
}

.header .nav > li > ul {
  padding: 10px 5px 8px;
  background: #c21a29;
  margin: 0 -5px;
  display: none;
}

.header .nav > li > ul > li {
  height: 40px;
  line-height: 40px;
  color: #fff;
}

.header .nav > li > ul > li > a {
  display: block;
}

.header .nav > li > ul > li > a:hover {
  background: #d91e2f;
}

.banner {
  width: 100%;
  height: 543px;
  margin: 13px auto 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.banner .content-slide {
  position: absolute;
  left: 50%;
  top: 35%;
  z-index: 99;
  color: #fff;
  text-align: left;
  margin-left: 70px;
}

.banner .content-slide .fl + .fl {
  margin-left: 10px;
  margin-top: 50px;
}

.banner .content-slide strong {
  font-size: 77px;
  color: #de0f21;
  float: right;
  line-height: 1.14;
}

.banner .content-slide strong + strong {
  font-size: 45px;
  clear: both;
}

.banner .content-slide span {
  font-size: 42px;
  color: #434343;
}

.banner .content-slide p {
  margin: 20px 0;
  padding: 0 20px;
  font-size: 18px;
  border-top: 1px dotted #a1a1a1;
  border-bottom: 1px dotted #a1a1a1;
  line-height: 28px;
}

.banner .content-slide a {
  display: block;
  margin-left: 30px;
  width: 123px;
  height: 41px;
  line-height: 41px;
  text-align: center;
  background: #c21a29;
  transition: background 0.2s;
}

.banner .content-slide a:hover {
  background: #95141f;
}

.pagination {
  position: absolute;
  z-index: 20;
  bottom: 20px;
  width: 100%;
  text-align: center;
}

.i_box1 {
  height: 365px;
  position: relative;
  overflow: hidden;
}

.i_box1 .i_box1_bg {
  width: 100%;
  height: 303px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  background: #2d2e32;
}

.i_box1 .fl {
  padding-top: 40px;
  position: relative;
  z-index: 2;
}

.i_box1 .fl a {
  width: 109px;
  height: 34px;
  line-height: 34px;
  text-align: center;
  text-decoration: underline;
  font-size: 12px;
  color: #c21a29;
  float: right;
  transition: all .4s;
}

.i_box1 .fl a:hover {
  background-color: #c21a29;
  color: #fff;
}

.i_box1 .text {
  width: 700px;
  max-height: 150px;
  color: #fff;
  line-height: 1.8;
  overflow: hidden;
}

.i_box1 .fr {
  position: absolute;
  left: 50%;
  top: 120px;
  width: 100%;
  height: 245px;
  margin-left: 130px;
  background-color: #c21a29;
  z-index: 3;
}

.i_box1 .fr img {
  margin-left: 20px;
  margin-top: -90px;
}

.i_tit {
  margin-bottom: 15px;
  width: 273px;
  height: 43px;
  background: url(../img/i_tit.jpg) no-repeat left bottom;
}

.i_tit span:first-child {
  color: #fff;
  font-size: 18px;
  margin-right: 10px;
}

.i_tit span + span {
  color: #ccc;
}

.i_box2 {
  margin-top: 85px;
}

.i_box2 ul li {
  float: left;
  width: 200px;
  padding: 1px;
  text-align: center;
}

.i_box2 ul li .i_box2_img {
  height: 173px;
  line-height: 173px;
  border: 1px solid #b9b9b9;
  margin-bottom: 15px;
}

.i_box2 ul li .i_box2_img:hover {
  border-color: #c01b29;
  background-color: #fff7f8;
}

.i_box2 ul li img {
  vertical-align: middle;
}

.i_box2 ul li a {
  display: block;
  font-size: 16px;
  color: #434343;
  transition: color 0.2s;
}

.i_box2 ul li a:hover {
  color: #f25618;
}

.money1 {
  text-decoration: line-through;
}

.pro_list ul li {
  float: left;
  padding: 15px;
  border: 1px solid #eee;
}

.pro_list ul li:hover {
  border-color: #f25618;
}

.pro_list ul li dl {
  padding: 0 3px 0 6px;
}

.pro_list ul li dl dt {
  color: #000;
}

.pro_list ul li dl dt:hover {
  color: #c21a29;
}

.pro_list ul li dl dd {
  font-size: 12px;
  color: #acacac;
}

.pro_list ul li dl dd .money1 {
  color: #666;
}

.pro_list ul li dl dd .money2 {
  color: #ff3c00;
}

.i_box3 {
  margin-top: 80px;
  padding-bottom: 150px;
  background-color: #2d2e32;
}

.i_box3 .menu {
  text-align: center;
  position: relative;
  top: -30px;
}

.i_box3 .menu ul {
  display: -moz-inline-stack;
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  zoom: 1;
  *display: inline;
}

.i_box3 .menu ul:hover .active {
  margin-top: 30px;
  height: 60px;
  line-height: 60px;
  background-color: #4c4d4f;
  color: #ccc;
}

.i_box3 .menu li {
  float: left;
}

.i_box3 .menu li a {
  display: block;
  margin-top: 30px;
  padding: 0 35px;
  height: 60px;
  line-height: 60px;
  background-color: #4c4d4f;
  color: #ccc;
  transition: all .2s;
}

.i_box3 .menu li a:hover, .i_box3 .menu li a.active, .i_box3 .menu li a.active:hover {
  margin-top: 0;
  height: 90px;
  line-height: 90px;
  background-color: #c21a29;
  color: #fff;
}

.i_box3 .list1 ul li {
  border-color: #434343;
}

.i_box3 .list1 ul li:hover {
  border-color: #c21a29;
}

